.form {
  @include below($tablet-width) {
    margin: 0 auto;
    max-width: 320px;
  }

  h3 {
    @include font-size (1.06em, 1.64, 600);
    margin: 0;
    text-align: center;
    color: $basic-white;
    margin-bottom: 1.25em;

    @include below($tablet-width) {
      color: $basic-black;
    }
  }
}

.form__list {
  @include list-reset;
}

.form__item {
  position: relative;
  margin-bottom: 1.25em;

  input[type="text"],
  input[type="tel"] {
    margin: 1rem 0;
    padding: 1.2em;
    color: $basic-black;
    border: none;
    border: 2px solid $basic-grey100;
    width: 100%;
    border-radius: 5px;

    &:focus {
      outline-color: $accent-color;
    }
  }

  label {
    position: absolute;
    top: 50%;
    left: 5%;
    transform: translate(-5%, -50%);
    font-size: 1.2rem;
    padding: 0rem 0.4rem;
    transition: all 0.4s ease;
    pointer-events: none;
  }

  .form__input:focus + label,
  .form__input:valid + label,
  .form__input:invalid:not(:placeholder-shown) + label {
    top: 30%;
    font-size: 0.6rem;
    color: $accent-color;
    background-color: $transparent;
  }
}
